@use "global" as *;

//visual
.visual {
    @include mq(md) {
    }
}

.visual__inner {
}

.visual__content {
    position: relative;
}

.visual__text {
    letter-spacing: rem(0.5);
    font-weight: $bold;
    z-index: 10;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    @include mq(md) {
    }
}

.visual__title,
.visual__subtitle {
}


.visual__title {
    font-size: rem(28);
    line-height: rem(28);

    @include mq(md) {
        font-size: rem(50);
        line-height: rem(50);
        font-weight: bold;
    }
}

.visual__subtitle {
    font-size: rem(20);
    line-height: rem(20);
    margin-top: rem(24);

    @include mq(md) {
        font-size: rem(25);
        line-height: rem(25);
        font-weight: bold;
    }
}